<template>
    <!--<div>
        综合信息统计

    </div>-->
    <div id="echarts" :style="{width: '800px', height: '300px'}"></div>
</template>

<script>
    export default {
        name: "StaAll",
        data() {
            return {
                allDeps: [],
            }
        },
        mounted() {
            this.initData();
            var dom = document.getElementById('echarts')
            var myChart = this.echarts.init(dom)
            // 绘制图表
            myChart.setOption({
                color: ['#3398DB'],
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                        type: 'line'        // 默认为直线，可选为：'line' | 'shadow'
                    }
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: [
                    {
                        type: 'category',
                        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
                        axisTick: {
                            alignWithLabel: true
                        }
                    }
                ],
                yAxis: [
                    {
                        type: 'value'
                    }
                ],
                series: [
                    {
                        name: '该部门人数',
                        type: 'bar',
                        barWidth: '60%',
                        data: [10, 52, 200, 334, 390, 330, 220]
                    }
                ]
            });
        },

        methods : {
            initData() {
                alert("111111111111");
                if (!window.sessionStorage.getItem("deps")) {
                    this.getRequest('/employee/statistics/deps').then(resp => {
                        if (resp) {
                            alert("222222222222");
                            this.allDeps = resp;
                            console.log(this.allDeps);
                            window.sessionStorage.setItem("deps", JSON.stringify(resp));
                        }
                    })
                } else {
                    this.allDeps = JSON.parse(window.sessionStorage.getItem("deps"));
                }

            }
        }
    }
</script>

<style scoped>

</style>